export function strip(html){
    let doc = new DOMParser().parseFromString(html, 'text/html');
    return doc.body.textContent || "";
 }

export const mdToTxt = (md)=>{
    if( !md ){
        return '';
    }

    let html = marked(md);
    return strip(html);
}

export const firstNCharts = (str, nChart) =>{
    if( !str || !str.length || str.length<nChart){
        return str;
    }

    return str.substring(0,nChart)+' ... ';
};

export const mdRenderLinkInNewTab = (props) => {
    if (!props.href.startsWith('http')) {
      return props.children;
    }
  
    return <a href={props.href} rel='nofollow noreferrer noopener' target='_blank'>{props.children}</a>;
};

export const mdRenderImgMaxWidth100percent = ({alt,src,title}) =>{
    return <img 
                alt={alt} 
                src={src} 
                title={title} 
                style={{ maxWidth: '100%' }}  />;

}